import React, { useRef, useState, memo, Fragment } from 'react';
import cls from './StaticAccordionModule.module.scss';
import { CheckedStyle, componentsGrey } from '../../../assets/images/icons';
import FieldRow from '../../common/UI/CustomFields/FieldRow/FieldRow';
import Loader from 'react-loader-spinner';
import { isMobile, isSafari } from 'react-device-detect';
import classNames from 'classnames';
import MessageContainer from '../ProcessesComps/MessageContainer/MessageContainer';
import 'react-phone-input-2/lib/bootstrap.css';
import { useProcessTaskContext } from '../../../context/processTask.context';
import ModuleLists from './ModuleLists';
import * as R from 'ramda';
import moment, { now } from 'moment';
import { Button } from '../../common';
import MapView from '../../common/Mapping/MapView';
import { useSelector } from 'react-redux';
import VideoConference from '../../common/VideoConference/VideoConference';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLocationDot } from '@fortawesome/free-solid-svg-icons';
import GoogleMaps from '../../common/Mapping/GoogleMaps/GoogleMaps';
import { computeFormula, isJson } from '../../../shared/utility';

const StaticAccordionModule = props => {
	const { component, blueprintIndex, companyData, components, allComponents, companyUsersArray, selectedComponent } = props;
	const [processTaskContext] = useProcessTaskContext();
	const [setActive,] = useState(cls.active);
	const [message] = useState('');
	const content = useRef(null);
	const indexInContextArray = processTaskContext.currentStep?.component_blueprints.findIndex(c => c.id === component.id);

	const hasMap = component.selected_fields.filter(sf => sf.data_type_name === 'Pin' || sf.data_type_name === 'Map').length > 0;
	return (
		<div id="accordion" className={cls.section} key={blueprintIndex}>
			<div className={classNames(cls.accordionContainer, setActive)} app-variant-has-error={message ? 'error' : ''} style={isMobile ? (hasMap ? { padding: '0', border: '0' } : { border: '0' }) : {}}>

				{!(hasMap && isMobile) && <p className={cls.cardTitle}>{component?.name}</p>}
				<div ref={content} style={{ maxHeight: setActive === cls.active ? '' : '0px', display: 'flex' }} className={cls.content}>
					<RenderSelectedFields
						components={components}
						allComponents={allComponents}
						component={component}
						handleInputChange={() => { }}
						handlePrefixChange={() => { }}
						handleCurrencyChange={() => { }}
						checkIfAreMandatoryErrored={false}
						handleCheckbox={() => { }}
						isInherited={false}
						indexInContextArray={indexInContextArray}
						handleLocationChange={() => { }}
						processTaskContext={processTaskContext}
						companyData={companyData}
						companyUsersArray={companyUsersArray}
						selectedComponent={selectedComponent}
					/>
				</div>
			</div>
			{message && message?.errors?.title && <MessageContainer message={message.errors.title} />}
			{message && message?.message && <MessageContainer message={message.message} />}
		</div>
	);
};

const RenderSelectedFields = memo(props => {
	const { component, components, companyData, allComponents, companyUsersArray, selectedComponent } = props;
	const { allModules, allTasks, companyPositionsArray, map } = useSelector(store => store.flowReducer);

	const getComponentsForListing = options => {
		return components
			.filter(component => component.id === options.component_id)
			.map(cmp => {
				let object = {};
				options.list.map(column => {
					object = { ...object, [column]: cmp.selected_fields.find(field => field.field_name === column).value };
				});
				return object;
			});
	};

	const allSelectedFields =
		component?.selected_fields?.length > 0 &&
		R.sortWith([R.ascend(R.prop('index'))])(component?.selected_fields
			?.filter(sf => sf.value !== true || sf?.value !== undefined || (sf?.data_type_name === 'Transaction List')))
			.map((field, index) => {

				let value = selectedComponent.component_fields.find(cf => cf.field_name === field?.field_name && cf.data_type_name === field?.data_type_name)?.value;


				if (field?.options?.list?.length > 0 && field?.options?.component_id?.length > 0 && (field?.data_type_name === 'Step List' || field?.data_type_name === 'Component List')) {
					return (
						<div key={index} style={{ width: '100%' }}>
							<ModuleLists columns={field.options.list} list={getComponentsForListing(field.options)} />
						</div>
					);
				} else if (field.data_type === 'output' && field.data_type_name !== 'Formula' && field.data_type_name !== 'Item Formula' && field.data_type_name !== 'Record' && field.data_type_name !== 'Filters') {
					return null;
				} else if (field.data_type_name === 'Info') {
					return null;
				} else if (field.data_type_name === 'Transaction List') {
					return null;
				} else if (field.data_type_name === 'URL') {
					return null;
				} else if (field.data_type_name === 'Ratings') {
					return null;
				} else if (field.data_type_name === 'Rewards') {
					return null;
				} else if (field.data_type_name === 'Map') {

					const foundM = JSON.parse(value ? value : (field?.value ?? '{}'));
					if (foundM?.routes?.DRIVE) {
						map.routes = { DRIVE: foundM.routes.DRIVE }
						map.pins = foundM?.pins ?? [];
					} else if (foundM?.pins) {
						map.pins = foundM?.pins;
						map.routes = { DRIVE: [] }

					}
					return <div key={index} />

				} else if (field.data_type_name === 'Lookup') {
					return null;
				} else if (field.data_type_name === 'Calendar') {
					return null;
				} else if (field.data_type_name === 'Summary') {
					return null;
				} else if (field.data_type_name === 'ID') {

					return (
						<FieldRow
							key={index}
							width={isMobile ? '100%' : '50%'}
							fieldLabel={field.field_name}
							style={{ border: '0' }}
							fieldIcon={field.icon}
							isProcess
							visible={field.visible}
							mandatory={false}
							error={field.errors}
							customIconBgColor={'white'}
							enabled={false}
						>
							<input readOnly style={{ pointerEvents: 'none' }} type={field.data_type} onChange={() => { }} autoComplete="none" value={value ?? field?.value} className={cls.fieldTypeInput} name={field.field_name} />
						</FieldRow>
					);
				} else {
					const infoField = component?.selected_fields?.find(sf => (sf.data_type_name === 'Info' && sf.visible === true));
					if (infoField) {
						const found = allComponents.find(c => c?.component_fields.filter(cf => cf.component_blueprint_id === infoField.options.component_id && cf.value === value).length > 0);
						if (found) {
							return infoField?.options?.list?.map((label, ix) => {
								const val = found.component_fields.find(cf => cf.field_name === label)?.value ?? '';

								return val?.includes('jpg') || val?.includes('jpeg') || val?.includes('png') ? (
									<FieldRow
										width={isMobile ? '100%' : '50%'}
										style={{ border: '0' }}
										key={index}
										fieldIcon={field.icon}
										isProcess
										visible={field.visible}
										mandatory={false}
										error={field.errors}
										customIconBgColor={'white'}
									>
										<div className={cls.accordionContainer} onClick={() => window.open(value, '_blank')}>
											<img alt="lg" onError={(e) => e.target.src = 'https://i.quotev.com/b2gtjqawaaaa.jpg'} src={val} style={{ width: '100%', height: '100%', cursor: 'pointer' }} />
										</div>
									</FieldRow>
								) : (
									<FieldRow enabled={false} width={isMobile ? '100%' : '50%'} key={ix} style={{ border: '0' }} fieldLabel={label} fieldIcon={field.icon} isProcess visible={field?.value !== undefined ? true : field.visible} mandatory={false} error={field.errors}>
										{val.length > 55 ? (
											<textarea className={cls.fieldTypeTextarea} type={field.data_type} value={val} onFocus={() => { }} autoComplete="none" onChange={e => { }} readOnly style={{ backgroundColor: 'rgba(234, 231, 231, 0.91)', height: 'auto', padding: '10px' }} />
										) : (
											<input readOnly style={{ pointerEvents: 'none' }} autoComplete="none" value={val} className={cls.fieldTypeInput} name={field.field_name} />
										)}
									</FieldRow>
								)
							});
						}
					}
					if (field.data_type_name === 'Pin' && value) {
						const location = JSON.parse(value);
						return (
							<div key={index} style={{ display: '', width: '100%', pointerEvents: 'none' }}>
								{field.field_name}
								<MapView
									mapkey={companyData.misc_info?.map}
									pins={
										location?.realtime
											? location?.destination?.address ? [
												{ ...location.destination, type: 'destination' },
												{ ...location.source, type: 'source' },
												{ ...location.realtime, type: 'realtime' },
											] :
												[
													{ ...location.source, type: 'source' },
													{ ...location.realtime, type: 'realtime' },
												]
											: location?.destination?.address ? [
												{ ...location.destination, type: 'destination' },
												{ ...location.source, type: 'source' },
											] :
												[
													{ ...location.source, type: 'source' },
												]
									}
								/>

								<div className={cls.filtersContainer} style={{ color: 'var(--app-color-gray-dark)', height: 'inherit', width: '100%', display: field?.visible ? '' : 'none' }}>
									<div className={cls.actionsContainer} style={{ fontSize: '12px' }}>
										{location?.destination?.address ? <div style={{ width: '100px', fontWeight: 'normal', fontSize: '14px' }}>{'From'}:</div> : <FontAwesomeIcon icon={faLocationDot} style={{ color: 'green', height: '20px', width: '20px' }} />}
										<div>{location.source.address}</div>
									</div>
									{location?.destination?.address && <hr style={{ borderTop: '0.5px solid #D2D2D2' }} />}
									{location?.destination?.address && <div className={cls.actionsContainer} style={{ fontSize: '12px' }}>
										<div style={{ width: '100px', fontWeight: 'normal' }}>{'To'}:</div>
										<div>{location?.destination?.address}</div>
									</div>}
								</div>
							</div>
						);
					}
					if (field?.data_type_name === 'Action Button' && field?.options?.action?.length > 0) {

						const usr = companyUsersArray?.find(user => (user.id === value));

						return (
							<FieldRow width={isMobile ? '100%' : '50%'} key={index} style={{ border: '0' }} isProcess
								fieldLabel={field.options?.notes ? field.options?.notes : field.field_name}
								enabled={false}
								visible={field.visible} mandatory={false} error={field.errors}>
								{field.options.action === 'user' &&
									<input
										style={{ pointerEvents: 'none' }}
										onChange={() => { }}
										autoComplete="none"
										value={usr ? `${usr?.first_name} ${usr?.last_name}` : (field?.value ?? '-')}
										className={cls.fieldTypeInput}
										name={field.field_name}
									/>
								}
							</FieldRow>
						);
					}
					if (field.data_type_name === 'Link') {
						return (
							<FieldRow
								width={isMobile ? '100%' : '50%'}
								style={{ border: '0' }}
								key={index}
								fieldIcon={field.icon}
								isProcess
								visible={field?.value !== undefined ? true : field.visible}
								mandatory={false}
								error={field.errors}
								customIconBgColor={'white'}
							>
								{value?.includes('jpg') || value?.includes('jpeg') || value?.includes('png') || field?.options?.image ? (
									<div className={cls.accordionContainer} onClick={() => window.open(value, '_blank')}>
										<img alt="lg" onError={(e) => e.target.src = 'https://i.quotev.com/b2gtjqawaaaa.jpg'} src={value ?? 'https://i.quotev.com/b2gtjqawaaaa.jpg'} style={{ width: '100%', height: '100%', cursor: 'pointer' }} />
									</div>
								) : value?.includes('pdf') ? (
									<div className={cls.accordionContainer} onClick={() => window.open(value, '_blank')}>
										<embed onClick={() => window.open(value, '_blank')} src={`${value}#toolbar=0&navpanes=0&scrollbar=0`} width="400px" height="500px" />
									</div>
								) : (
									<div style={{ width: 'inherit', textDecoration: 'underline', textAlign: 'center', cursor: 'pointer' }} onClick={() => window.open(value, '_blank')}>
										{'Link'}
									</div>
								)}
							</FieldRow>
						);
					}
					if (field.data_type_name === 'Freehand') {
						return (
							<FieldRow
								width={isMobile ? '100%' : '50%'}
								style={{ border: '0' }}
								key={index}
								fieldIcon={field.icon}
								isProcess
								visible={field?.value !== undefined ? true : field.visible}
								mandatory={false}
								error={field.errors}
								customIconBgColor={'white'}
							>
								<div className={cls.accordionContainer} onClick={() => window.open(value, '_blank')}>
									<img alt="lg" onError={(e) => e.target.src = 'https://i.quotev.com/b2gtjqawaaaa.jpg'} src={value ?? 'https://i.quotev.com/b2gtjqawaaaa.jpg'} style={{ width: '100%', height: '100%', cursor: 'pointer' }} />
								</div>
							</FieldRow>
						);
					}
					if (field.data_type_name === 'Checkbox') {
						return (
							<FieldRow
								width={isMobile ? '100%' : '50%'}
								key={index}
								style={{ border: '0' }}
								fieldLabel={field.options.value}
								fieldIcon={field.icon}
								isProcess
								visible={field?.value !== undefined ? true : field.visible}
								mandatory={false}
								error={field.errors}
								customIconBgColor={'white'}
								enabled={false}
							>
								<input style={{ pointerEvents: 'none' }} onChange={() => { }} autoComplete="none" value={value === undefined ? '' : value == true ? 'Yes' : 'No'} className={cls.fieldTypeInput} name={field.field_name} />
							</FieldRow>
						);
					}
					if (field.data_type_name === 'YesNo') {
						return (
							<FieldRow
								width={isMobile ? '100%' : '50%'}
								key={index}
								style={{ border: '0.5px dashed gray', padding: '10px' }}
								fieldIcon={field.icon}
								isProcess
								visible={field?.value !== undefined ? true : field.visible}
								mandatory={false}
								error={field.errors}
								customIconBgColor={'white'}
								enabled={false}
							>
								<div style={{ display: 'flex', justifyContent: 'space-between', margin: '20px 0px' }}>
									<input id="check-23" type={'checkbox'} readOnly checked={value ? true : false} />
									<label>
										{field.field_name}
									</label>
								</div>
							</FieldRow>

						);
					}
					if (field.data_type_name === 'Upload') {
						return (
							<FieldRow key={index} fieldLabel={field.field_name} fieldIcon={field.icon} isProcess visible={value !== undefined ? true : value?.length > 0} mandatory={false} error={field.errors} customIconBgColor={'white'} style={{ border: '0', marginRight: '10px' }}>
								<a key={index} href={value} download="Example-PDF-document" target="_blank" rel="noreferrer">
									<Button clear title={'Download File'} />
								</a>
							</FieldRow>
						);
					}

					if (field.data_type_name === 'Status') {
						return (
							<FieldRow
								width={isMobile ? '100%' : '50%'}
								key={index}
								style={{ border: '0' }}
								fieldLabel={field.field_name}
								fieldIcon={field.icon}
								isProcess
								visible={field?.value !== undefined ? true : field.visible}
								mandatory={false}
								error={field.errors}
								customIconBgColor={'white'}
								enabled={false}
							>
								<input readOnly style={{ pointerEvents: 'none' }} onChange={() => { }} autoComplete="none" value={selectedComponent?.status ?? (field?.value ?? value)} className={cls.fieldTypeInput} name={field.field_name} />
							</FieldRow>
						);
					}
					if (field.data_type_name === 'Dropdown') {
						if (field.options === 'modules') {
							value = allModules.find(mod => mod.id === value)?.name;
						}
					}
					if (field.data_type_name === 'Positions') {
						value = companyPositionsArray?.find(cp => cp.value === value)?.label;
					}
					if (field.data_type_name === 'People') {
						if (field.options.calendar) {
							const info = JSON.parse(value ?? (field?.value ?? JSON.stringify({})));
							const found = companyUsersArray.find(user => user.id === info?.user);
							value = found ? `${found.first_name} ${found.last_name}` : '';
							if (info?.task) {
								const task = allTasks.find(task => task.id === info.task);
								return <FieldRow enabled={false} width={isMobile ? '100%' : '50%'} key={index} style={{ border: '0' }} fieldLabel={field.field_name} fieldIcon={field.icon} isProcess visible={field.visible} mandatory={false} error={field.errors}>
									<div style={{ display: 'flex', flexDirection: 'column', width: 'inherit' }}>
										<input
											style={{ pointerEvents: 'none' }}
											autoComplete="none"
											value={value}
											readOnly
											className={cls.fieldTypeInput}
											name={field.field_name}
										/>

										{task && <input
											style={{ pointerEvents: 'none' }}
											autoComplete="none"
											value={task?.name}
											readOnly
											className={cls.fieldTypeInput}
											name={field.field_name}
										/>}
										{task && <input
											style={{ pointerEvents: 'none' }}
											autoComplete="none"
											value={moment(new Date(task?.add_info?.start)).format('DD MMM YYYY h:mm a')}
											readOnly
											className={cls.fieldTypeInput}
											name={field.field_name}
										/>}
										{task?.status && <input
											style={{ pointerEvents: 'none' }}
											autoComplete="none"
											value={task?.status}
											readOnly
											className={cls.fieldTypeInput}
											name={field.field_name}
										/>}
									</div>
								</FieldRow>
							}

						} else {

							if (!value) {
								const found = companyUsersArray.find(user => user.id === field.value);
								value = found ? `${found.first_name} ${found.last_name}` : '';

							}


						}
					}
					if (field.data_type_name === 'Video' && selectedComponent?.status === (field?.options?.status ?? 'New')) {

						if ((field?.value || value) && (moment(field?.value ?? value) <= now() && now() < (moment(field?.value ?? value).add('minutes', +(field?.options?.expiry ?? 30))))) {
							return (
								<FieldRow width={'100%'} key={index} style={{ border: '1px dashed gray' }} fieldLabel={field.field_name} fieldIcon={field.icon} isProcess visible={field.visible} mandatory={false} error={field.errors}>
									<div className={cls.actionsContainer} style={{ display: 'flex', justifyContent: 'center', fontSize: '12px' }}>
										<VideoConference payload={{ name: selectedComponent.id }} />
									</div>
								</FieldRow>
							)
						}
					}
					return (
						<FieldRow enabled={false} width={isMobile ? '100%' : '50%'} key={index} style={{ border: '0' }} fieldLabel={field.field_name} fieldIcon={field.icon} isProcess visible={field.visible} mandatory={false} error={field.errors}>
							{value?.length > 50 ? (
								<textarea readOnly className={cls.fieldTypeTextarea} style={{ padding: '10px', pointerEvents: 'none' }} onFocus={() => { }} required={true} value={value === null || value === undefined ? '' : (field.data_type_name === 'Date' || field.data_type_name === 'Video') ? moment(new Date(value)).format('DD MMM YYYY') : value} onChange={e => { }} />
							) : (
								<input
									readOnly
									style={{ pointerEvents: 'none' }}
									onChange={() => { }}
									autoComplete="none"
									value={value === null || value === undefined ? '' : (field.data_type_name === 'Dropdown' && field.options?.type === 'modules') ? allModules.find(mod => mod.id === field.value)?.name : (field.data_type_name === 'Date' || field.data_type_name === 'Video') ? (moment(new Date(value)).format('DD MMM YYYY') === 'Invalid date' ? '' : moment(new Date(value)).format('DD MMM YYYY h:mm a')) : value}
									className={cls.fieldTypeInput}
									name={field.field_name}
								/>
							)}
						</FieldRow>
					);
				}
			});

	return (
		<div className={cls.emulatedFlexGap} style={{ width: isMobile ? '100%' : '' }}>
			{allSelectedFields}
		</div>
	);
});

export default StaticAccordionModule;

export const StaticAccordionCreator = props => {
	const [showId, setShowId] = useState(false);
	const { component } = props;

	return (
		<div className={cls.accordionContainer} style={{ border: isMobile ? '0' : '' }}>
			<div className={cls.emulatedFlexGap} style={{ width: isMobile ? '100%' : '100%' }}  >
				<FieldRow width={isMobile ? '100%' : '50%'} fieldLabel={'Created By'} style={{ border: '0' }} isProcess visible={true}>
					<input onClick={() => setShowId(!showId)} readOnly onChange={() => { }} autoComplete="none" value={`${component.created_by?.first_name} ${component.created_by?.last_name}`} className={cls.fieldTypeInput} />
				</FieldRow>
				<FieldRow width={isMobile ? '100%' : '50%'} fieldLabel={'Created Date'} style={{ border: '0' }} isProcess visible={true}>
					<input style={{ pointerEvents: 'none' }} onChange={() => { }} autoComplete="none" value={moment(new Date(component.created_date)).format('DD MMM YYYY h:mm a')} className={cls.fieldTypeInput} name={'Created Date'} />
				</FieldRow>
				<FieldRow width={isMobile ? '100%' : '50%'} fieldLabel={'Transaction ID'} style={{ border: '0' }} isProcess visible={showId}>
					<input onChange={() => { }} autoComplete="none" value={component.id} className={cls.fieldTypeInput} name={'CID'} />
				</FieldRow>
			</div>
		</div>
	);
};

export const StaticAccordionPayments = props => {
	const { component } = props;

	return (
		<div className={cls.accordionContainer} style={{ border: isMobile ? '0' : '', display: component.payments ? '' : 'none' }}>
			<div className={cls.emulatedFlexGap} style={{ width: isMobile ? '100%' : '100%' }}>
				<FieldRow width={isMobile ? '100%' : '50%'} fieldLabel={'Payments'} style={{ border: '0' }} isProcess visible={true}>
					<input style={{ pointerEvents: 'none' }} onChange={() => { }} autoComplete="none" value={`${component.payments?.stripe_product}, ID: ${component.payments?.stripe_id.substring(0, 13)}`} className={cls.fieldTypeInput} />
				</FieldRow>
				<FieldRow width={isMobile ? '100%' : '50%'} fieldLabel={'Amount Paid'} style={{ border: '0' }} isProcess visible={true}>
					<input style={{ pointerEvents: 'none' }} onChange={() => { }} autoComplete="none" value={`${component.payments?.stripe_price}`} className={cls.fieldTypeInput} />
				</FieldRow>
			</div>
		</div>
	);
};

export const StaticAccordionItems = props => {

	const { field, selectedComponent, companyUsersArray, component } = props;

	const GetInputField = props => {
		const { field, index, item } = props;

		if (field?.data_type_name === 'People' && field?.options?.position_id?.length > 0) {
			const found = companyUsersArray.find(usr => field?.value === `${usr.first_name} ${usr.last_name}`);
			return (
				<div style={{ display: 'flex', justifyContent: 'center' }}>
					{found && <div className={cls.avatarContentContainer}
					>
						<img alt="lg" src={found?.photo} className={cls.avatarUrlImg} />
					</div>}
				</div>

			);

		} else if (field.data_type === 'date' || field?.options?.country_code || field?.options?.currency_type || field.data_type_name === 'Number') {
			return ((field?.options?.display === 'bar') ?
				<div className={cls.fieldTypeInput} style={{ paddingLeft: '0px', border: 'none', height: 'auto' }}>

					<div style={{ height: '30px', width: '100%', backgroundColor: field?.options?.maxC, display: 'flex', flexDirection: 'row', border: 'none' }}

					>
						<input defaultValue={''} className={cls.fieldTypeInput} name={field.field_name}
							style={{ color: 'transparent', border: 'none', height: '30px', width: `${((+(field?.value) / +field.options.max) * 100)}%`, backgroundColor: field?.value ? field?.value === '0' ? 'transparent' : field?.options?.barC : 'transparent' }}
						/>
					</div>


				</div> :
				<div className={cls.fieldTypeInputSimple}
					style={{
						backgroundColor: field?.options?.valueC ? field?.options?.valueC : '',

					}}>
					{field?.data_type === 'date' ? moment(field.value).format(field?.options?.time ? 'DD MMMM YYYY HH:mm' : 'DD MMMM YYYY') : field?.value}
				</div>
			);

		} else if (field?.data_type_name === 'Item Formula' && field?.options?.operator?.length > 0) {

			const value = computeFormula(
				field?.options,
				component.selected_fields.filter(x => field.options.list.includes(x.field_name) && x.options.mapToItem),
				[],
				[],
				item,
				true
			);

			return (
				<div className={cls.fieldTypeInputSimple}
					style={{
						backgroundColor: field?.options?.valueC ? field?.options?.valueC : '',
					}}>
					{value}
				</div>
			);
		}

		return (
			<div className={cls.fieldTypeInputSimple}
				style={{
					backgroundColor: field?.options?.valueC ? field?.options?.valueC : '',
				}}>
				{field?.value}
			</div>
		);
	}


	const orderedObj = {
		item_code: null,
		quantity: null,
		value: null,
		alt_value: null,
		total: null,
	};

	const columnsObj = Object.entries(Object.assign(orderedObj, field.options['map'])).reduce((a, [k, v]) => (v === null ? a : ((a[k] = v), a)), {});

	let footerObj = Object.entries(Object.assign(orderedObj, field.options['map'])).reduce((a, [k, v]) => (v === null ? a : ((a[k] = v), a)), {});
	Object.keys(columnsObj).map(key => {
		if (key === 'item_code') {
			footerObj.item_code = 'Totals:';
		} else {
			if (key === 'quantity') {
				footerObj[key] = `${R.reduce((a, b) => +a + +b[key] ?? 0, 0, selectedComponent?.component_items ?? [])}`;
			} else if (key === 'alt_value') {
				footerObj[key] = ``;
			} else if (key === 'value') {
				footerObj[key] = ``;
			} else if (key === 'total') {
				footerObj[key] = ``;
			}
		}
	});
	const listHeaders = field?.options?.group ? component?.selected_fields?.find(sf => sf?.options?.mapToItem === field?.options?.groupL && sf.data_type_name === 'Dropdown')?.options?.list ?? [] : [undefined];

	return (

		<div style={{ width: '100%', border: '1px dashed gray', borderRadius: '10px', padding: '40px', fontFamily: 'var(--app-text-main-font)', margin: '20px 0' }}>

			<p style={{ fontSize: '20px', fontFamily: 'var(--app-text-main-font)', fontWeight: 'bold', color: 'var(--app-color-gray-dark)' }}>
				{field.field_name}
			</p>

			{listHeaders?.map((li, ix) => {

				const list = R.sortWith([R.ascend(R.prop('index'))])(selectedComponent?.component_items?.filter(it => it[field?.options?.groupL] === li) ?? [])?.map((itm, index) => {
					itm.index = index;
					let object = {
						index: index,

					};
					component?.selected_fields?.filter(sf => sf?.options?.mapToItem).map(fld => {

						const value = isJson(itm[fld.options.mapToItem]) ? JSON.parse(itm[fld.options.mapToItem] ?? '{}')[fld.field_name] : itm[fld.options.mapToItem];
						object[fld.field_name] =
							<GetInputField
								index={index}
								item={itm}
								field={{ ...fld, visible: true, value: value ?? '' }}
							/>
					})
					return object;
				});
				return <>
					<ModuleLists
						key={ix}
						withColumns={list.length === 0 ? false : true}
						className={cls.itemsContainer}
						columns={component?.selected_fields?.filter(sf => sf?.options?.mapToItem).map(sf => sf.field_name).slice(0, isMobile ? 3 : 8)}
						//footer={Object.values(footerObj)}
						list={list}
					>
						{field?.options?.group &&
							<p style={{ fontSize: '16px', fontFamily: 'var(--app-text-main-font)', fontWeight: 'bold', color: 'var(--app-color-gray-dark)' }}>
								{li}
							</p>}

					</ModuleLists>
					{list.length === 0 && <p>
						{'No items found'}
					</p>}
				</>
			}
			)}
		</div>

	);

}
